.sp-chatbox-iframe {
	/* don't remove anything, this is fighting other sites' styling*/
	min-height: 0px;
	min-width: 0px;
	height: 100%;
	width: 100%;
	max-width: 9999px;
	max-height: 9999px;
	border: none;
	/* put display: none here, but still need to set it in js
  so that we can read it easily later
  */
	margin-bottom: 0px;
}
.sp-chatbox-iframe-wrapper {
	position: fixed !important;
	/* left: 0; */
	bottom: 0;
	top: unset !important;
	z-index: 2147483647;
	/* one less than image modal */
	display: none;
	/* border: 1px solid lightgray; */
	border-bottom: none;
	/* border-right: none; */
	/* box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.31); */
	box-shadow: 0 2px 6px rgba(0, 0, 0, 0.5);
}
.sp-chatbox-drag-handle {
	margin-top: -30px;
	height: 30px;
	border-top-left-radius: 5px;
	border-top-right-radius: 5px;
	/* width: 50%; */
	/* margin: auto; */
	/* background-color: yellow; */
	text-align: center;
}
.sp-chatbox-drag-handle svg {
	display: none;
	font-size: 30px;
	color: gray;
}
.sp-chatbox-drag-handle:hover svg {
	display: inline-block;
}
.sp-chatbox-drag-handle:hover {
	background-color: rgba(255, 255, 255, 0.95);
	box-shadow: rgba(0, 0, 0, 0.5) 0px 2px 6px;
}

.sp-blue-button {
	background-color: #2196f3 !important;
}
.sp-chat-icon-wrapper {
	z-index: 2147483647;
	margin: 10px;
	width: 32px;
	height: 32px;
	cursor: pointer;
	position: fixed;
	right: 0;
	bottom: 0;
	color: #2096f3;
	border: 1px solid #2096f3;
	border-radius: 100%;
	text-align: center;
	line-height: 32px;
}
.sp-chat-icon-wrapper.vertical-center {
	bottom: 50%;
}
.sp-chat-icon-wrapper svg {
	vertical-align: unset;
}
.sp-chat-icon-wrapper img {
	width: 100%;
	height: 100%;
	/* 
  inline style is display none so we don't show the icon
  until css file is properly loaded
   */
	display: block !important;
}

div.sp-image-modal {
  z-index: 2147483647;
  /* iframe is 2147483646; */
}

.sp-image-modal img {
  width: 100%;
}

.sp-users-wrapper {
	position: fixed;
	/* right: 0; */
	bottom: 0;
	left: 5px;
	/* left: 50%; */
	/* margin-left: -50%; */
	z-index: 2147483646;
	display: flex;
	max-width: calc(100% - 55px);
	/* overflow-x: auto; */
}

.sp-avatar {
	height: 50px;
	width: 50px;
	/* position: fixed; */
	/* bottom: 0; */
	border-radius: 100%;
	background-size: cover;
	display: inline-block;
}

.sp-user-chat-bubble {
	position: absolute;
	bottom: 70px;
}
.sp-user-chat-bubble img {
	max-width: 50px;
	max-height: 100px;
}

.sp-user-chat-bubble.text {
	padding: 10px;
	background-color: white;
	border-radius: 5px;
	font-size: medium;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	min-width: 50px;
	max-width: 250px;
	overflow-wrap: break-word;
	border: 1px solid #666;
	text-align: center;
}

.sp-user-chat-bubble.text:after {
	content: " ";
	position: absolute;
	/* width: 29px; */
	height: 0;
	left: 25px;
	right: auto;
	top: auto;
	bottom: -12px;
	border: 6px solid;
	border-color: white transparent transparent white;
}

.sp-user-chat-bubble.text:before {
	content: " ";
	position: absolute;
	width: 0;
	height: 0;
	left: 24px;
	right: auto;
	top: auto;
	bottom: -14px;
	border: 7px solid;
	border-color: #666 transparent transparent #666;
}

.sp-user-popover {
	/* background: white; */
	padding: 5px;
	margin-bottom: 35px;
	color: rgb(255, 19, 19);
	cursor: pointer;
	z-index: 2147483647;
}
.sp-user-popover .blocked {
	color: greenyellow;
}
.sp-user-popover:hover {
	/* background: rgba(255, 255, 255, 0.493); */
	/* color: rgb(0, 38, 255); */
}

.sp-danmu-wrapper {
	display: flex;
	position: fixed;
	cursor: pointer;
	z-index: 2147483647;
}
.sp-danmu-avatar {
	/* display: inline-block; */
	border-radius: 100%;
	width: 35px;
	height: 35px;
	object-fit: cover;
	border: 1px solid lightgray;
}
.sp-danmu-room-name {
	font-size: 20px;
	line-height: 25px;
	padding: 5px;
}
.sp-danmu-content.text {
	border: 1px solid lightgray;
	background-color: white !important;
	color: black !important;
	padding: 5px;
	padding-left: 10px;
	padding-right: 10px;
	font-size: 20px;
	line-height: 25px;
	border-radius: 5px;
	max-width: 500px;
	margin-left: 5px;
	/* border: none; */
	overflow: hidden;
	text-overflow: ellipsis;
	word-break: break-word;
	/* box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.2); */
	text-align: left;
}
.sp-danmu-content.self.text {
	background-color: #bf0 !important;
	color: black !important;
}
